import { useEffect } from 'react';
import { App } from '@capacitor/app';

const useBackButton = (onBack: () => void) => {
  useEffect(() => {
    //    
    const backHandler = App.addListener('backButton', data => {
      // data.canGoBack     .
      //  (  )      
      onBack();
    });

    return () => {
      backHandler.then(h => h.remove());
    };
  }, [onBack]);
};

export default useBackButton;
